<template>
	<div class="row mt-4 form-pagination">
		<div class="col-12 col-sm-4 text-center text-sm-start">
			<button
				v-if="show_back"
				@click="prev"
				type="button"
				class="btn btn-light btn-left btn-lg"
			>
				Nasp
			</button>
		</div>
		<div class="col-12 col-sm-4 text-center">
			<button
				@click="clear"
				type="button"
				class="btn btn-white btn-lg"
			>
				Vymaza daje
			</button>
		</div>
		<div class="col-12 col-sm-4 text-center text-sm-end">
			<button
				@click="next"
				:disabled="disable_next"
				:title="disable_next ? 'Pokame sa zopakova poiadavku.' : ''"
				type="button"
				class="btn btn-primary btn-right btn-lg"
			>
				Pokraova
			</button>
		</div>
	</div>
</template>

<script>
	import STATE_core from '@/store/STATE_core.js';
	import STATE_params from '@/store/STATE_params.js';
	import STATE_error from '@/store/STATE_error.js';
	import STATE_forms from '@/store/STATE_forms.js';

	export default {
		props: {
			show_back: Boolean,
			disable_next: Boolean,
		},
		components: {
		},
		emits: ['next', 'prev'],
		data() {
			return {
			}
		},
		created() {
		},
		methods: {
			next() {
				this.$emit('next');
			},
			prev() {
				this.$emit('prev');
			},
			clear() {
				STATE_params().$reset();
				STATE_error().$reset();
				STATE_forms().$reset();
				STATE_core().set_session_token('', true);
				STATE_core().change_step(-1);
				setTimeout(
					() => { STATE_core().change_step(1) },
					100
				);
			},
		},
		computed: {
		},
	}
</script>
