<template>
	<input
		:id="field_id"
		:name="field_id"
		v-model="value"
		:placeholder="field_data.placeholder"
		:disabled="field_data.type_settings?.disabled"
		@blur="transform_value"
		maxlength="50"
		type="text"
		class="form-control"
	>
</template>

<script>
	import STATE_params from '@/store/STATE_params.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
		},
		components: {
		},
		emits: [],
		data() {
			return {
				dynamic: {
					majetok_podlazie: 'majetok_poschodie',
				},
			}
		},
		created() {
		},
		methods: {
			transform_value()
			{
				if (this.value !== this.value.trim()) {
					this.value = this.value.trim();
				}
				if (this.field_data.type_settings.transform === 'uppercase') {
					this.value = this.value.toUpperCase();
				} else if (this.field_data.type_settings.transform === 'capitalize') {
					// first character upper, rest of string left alone
					this.value = this.value.charAt(0).toUpperCase() + this.value.slice(1);
				}
			},
		},
		computed: {
			/* peto told me this is not in use anymore
			html_validation()
			{
				// TODO html validation via attributes, f.e. maxlength="10"
				if (this.field_data.attributes === undefined) {
					return '';
				} else {
					var attributes = '';
					for (var key in this.field_data.attributes) {
						attributes += key + '="' + this.field_data.attributes[key] + '" ';
					}
					return attributes;
				}
			},*/
			dynamic_parent()
			{
				if (!this.dynamic.hasOwnProperty(this.field_id)) {
					return null;
				}
				return STATE_params().get_user_input(this.dynamic[this.field_id]);
			},
			value:
			{
				get() {
					return STATE_params().get_user_input(this.field_id);
				},
				set(event_value) {
					STATE_params().set_user_input(this.field_id, event_value);
				},
			},
		},
		watch: {
			dynamic_parent()
			{
				if (isNaN(this.dynamic_parent)) {
					this.value = '';
				} else {
					this.value = parseInt(this.dynamic_parent, 10) + 1;
				}
			},
		},
	}
</script>
