<template>
	<div
		@click="did_click_outside"
		class="vue_modal"
		:class="huge ? 'huge' : ''"
	>
		<div class="modal-content">
			<div class="modal-header">
				<h4 class="mb-0">
					{{ title }}
				</h4>
				<span
					v-if="close"
					@click="close_me"
					class="close"
				>
					&times;
				</span>
			</div>
			<hr>
			<div class="modal-body">
				<div class="row">

					<slot></slot>

				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import STATE_modals from '@/store/STATE_modals.js';

	export default {
		props: {
			close: Boolean,
			title: String,
			huge: Boolean, // optional
		},
		components: {
		},
		emits: [],
		data() {
			return {
			}
		},
		created() {
			// close modal on Esc
			document.addEventListener(
				'keyup',
				(event) => {
					if (event.keyCode === 27) {
						this.close_me();
					}
				}
			);
		},
		methods: {
			close_me()
			{
				if (!close) {
					return;
				}
				STATE_modals().close_them_all();
			},
			did_click_outside(event)
			{
				if (event.target.classList.contains('vue_modal')) {
					this.close_me();
				}
			},
		},
		computed: {
		},
		watch: {
		},
	}
</script>

<style lang="scss" scoped>
	.vue_modal {
		/* The Modal (background) */
		display: block;
		position: fixed;
		z-index: 1;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		overflow: auto;
		background-color: rgba(0,0,0,0.4);

		/* Modal Content/Box */
		.modal-content {
			margin: 15% auto;
			padding: 20px;
			width: 80%;
			max-width: 900px;
			background-color: ghostwhite !important;
		}
		&.huge .modal-content {
			margin: 4% auto;
			width: 92%;
			max-width: 1300px;
		}

		/* The Close Button */
		.close {
			color: #aaa;
			float: right;
			font-size: 28px;
			font-weight: bold;
		}

		.close:hover,
		.close:focus {
			color: black;
			text-decoration: none;
			cursor: pointer;
		}
	}
</style>
